* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1,
h2,
h3,
h4 {
  font-weight: 400 !important;
}
body {
  font-family: "Roboto", sans-serif;
  /* font-family: "Raleway", sans-serif; */
}
