:root {
  --primary-color: #fdc400;
  --border: 2px solid rgb(255, 187, 0);
  --box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
}
/* utility classes */
.dflex {
  display: flex;
}
.dflex-all {
  display: flex;
  align-items: center;
  justify-content: center;
}
.justify-content {
  justify-content: center;
}
.justify-content-space-bet {
  justify-content: space-between;
}
.align-items {
  align-items: center;
}
.flex-col {
  flex-direction: column;
}
.active {
  display: flex;
}
.inActive {
  display: none;
}
/* Header top style */
.header-wrap {
  background-color: #383838;
  /* display: none; */
}
.header-top {
  margin: auto;
  height: 3rem;
  width: 80%;
  justify-content: space-between;
}

address {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
  font-style: normal;
}
address i,
address a i {
  color: rgb(248, 247, 247);
  margin-right: 10px;
}

address a,
ul.social-icons li a {
  color: white;
  text-decoration: none;
}
.social-icons li {
  margin: 0 13px;
}
.social-icons {
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: space-between;
  list-style: none;
}
address:hover i,
address:hover,
address:hover a,
.social-icons li a:hover {
  color: var(--primary-color);
}
.address a span {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}
address:hover .bi-telephone-outbound.bi::before {
  /* animation properties */
  animation-name: my-animation;
  animation-duration: 0.05s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes my-animation {
  from {
    transform: translateX(-1.2px);
  }
  to {
    transform: translateX(1.2px);
  }
}

.arrow {
  height: 4rem;
  font-size: 1.5rem;
  color: white;
  background: #383838;
  cursor: pointer;
}
/* header bottom means Navbar style */
.navbar-main {
  /* border: var(--border); */
  background: white;
  z-index: 102;
  width: 100%;
  justify-content: space-around;
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
  position: sticky;
  top: -1rem;
}
.navbar-wrapper {
  height: 9rem;
  align-items: center;
  width: 85%;
  margin: 0 auto;
}
.navbar {
  width: 50%;
}
.nav-item-container {
  justify-content: space-around;
  width: 100%;
  list-style: none;
}
.nav-item-container li {
  padding: 0 10px;
  /* margin: ; */
}
.nav-item-container li a {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  color: black;
  font-weight: 600;
  font-size: 1rem;
  text-decoration: none;
  /* padding: 4px 20px; */
  overflow: hidden;
  position: relative;
}
.nav-item-container li a:active {
  color: var(--primary-color);
}
.nav-item-container li a::before {
  content: attr(data-hover);
  top: 0;
  left: 0;
  /* padding: 4px 20px; */
  position: absolute;
  /* padding: 4px 20px; */
}
.nav-item-container li:hover a,
.phone-footer:hover a span {
  color: var(--primary-color);
}
.nav-item-container li:hover a::before {
  color: var(--primary-color);
  transition: all 0.21s ease-in-out;
  animation: menu 0.21s linear 1;
}
@keyframes menu {
  0% {
    color: var(--primary-color);
    top: 20%;
  }
  100% {
    top: 0;
  }
}
.toggle-menu-container {
  height: 3rem;
  width: 12%;
  margin-right: 1rem;
}
.toggle-menu-container .toggle-menu.menu-on i {
  font-size: 2.5rem;
}
.toggle-menu-container .toggle-menu.menu-on i::before {
  font-size: 2.5rem;
  font-weight: bold !important;
  /* border: var(--border); */
}
.toggle-menu-container .toggle-menu.menu-off i {
  /* border: var(--border); */
  padding: 0 !important;
  margin: 0 !important;
  font-size: 3rem;
  font-weight: bold;
}

/* ```````````````````````````````````````section1 styling  ``````````````````````````````````````*/
.section1 {
  flex-direction: column;
  overflow: hidden;
}
.img-container {
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 65vh;
  text-align: left;
  color: white;
  font-size: 5rem;
  transition: all 0.5s linear;
  animation: imgSlider 30s forwards infinite;
  position: relative;
  overflow: hidden;
}
@keyframes imgSlider {
  0% {
    background: url(/IndianDrivingSchool/static/media/bg2.cb7566d2.jpg) center center/cover;
  }
  45% {
    background: url(/IndianDrivingSchool/static/media/bg2.cb7566d2.jpg) center center/cover;
  }
  50% {
    background: url(/IndianDrivingSchool/static/media/bg.9df9f89a.avif) center center/cover;
  }
  95% {
    background: url(/IndianDrivingSchool/static/media/bg.9df9f89a.avif) center center/cover;
  }
  100% {
    background: url(/IndianDrivingSchool/static/media/bg2.cb7566d2.jpg) center center/cover;
  }
}
.color {
  color: var(--primary-color);
}
.img-container div.text-div {
  width: 85%;
  text-transform: capitalize;
  animation: sliding 30s forwards infinite;
  transform: translateY(300%);
  animation-delay: -2s;
}
.img-container div.text-div p,
.img-container div.text-div-alt p {
  font-size: 2rem;
  padding: 0.5rem;
  text-align: left;
  text-transform: none;
}

.img-container div.text-div-alt {
  transform: translateY(300%);
  width: 85%;
  text-transform: capitalize;
  animation: text-alt 30s forwards infinite;
  animation-delay: 13s;
}
@keyframes sliding {
  0% {
    /* font-size: 1em; */
    visibility: visible;
    transform: translateY(200%);
    opacity: 0;
  }
  25% {
    transform: translateY(40%);
    visibility: visible;
    opacity: 1;
  }

  50% {
    transform: translateY(40%);
    visibility: hidden;
  }
  75% {
    transform: translateY(40%);
    visibility: hidden;
  }
  100% {
    visibility: hidden;
  }
}

/* @keyframes text-alt {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: hidden;
  }
  50%{
    transform: translateY(300%);
    visibility: visible;
    
  }
  75%{
    transform: translateY(-100%);
    visibility: visible;
    
  }
  100% {
    transform: translateY(-100%);
    visibility: visible;
  }
} */

@keyframes text-alt {
  0% {
    visibility: visible;
    transform: translateY(200%);
    opacity: 0;
  }
  25% {
    transform: translateY(-50%);
    visibility: visible;
    opacity: 1;
  }

  50% {
    transform: translateY(-50%);
    visibility: hidden;
  }
  75% {
    visibility: hidden;
  }
  100% {
    visibility: hidden;
    /* visibility: visible; */
    /* background: red;  */
  }
}

.total-data-container {
  background: url(/IndianDrivingSchool/static/media/bgimg.9a4a50eb.jpg);
  height: 7rem;
  width: 100%;
  position: relative;
  /* z-index: -1; */
}
.total-data {
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: cneter;
  color: white;
  font-size: 2.5rem;
  z-index: 1;
  background: url(/IndianDrivingSchool/static/media/bgimg.9a4a50eb.jpg);
}
.total-data-items {
