    font-size: 32px;
    font-size: 2.3rem;
  }
  .img-container div.text-div p,
  .img-container div.text-div-alt p {
    font-size: 1.5rem;
    padding: 0.5rem 0;
    text-align: left;
    text-transform: none;
  }
  /* section2 */
  .right-mark-text,
  .section2-container .section2-text p {
    padding: 1rem 2rem;
    font-size: 1.1rem;
    line-height: 2rem;
    font-family: "Raleway", sans-serif;
  }
  .section2-container .section2-text h1,
  .section4-heading {
    padding: 0;
    text-align: center;
    font-size: 2.7rem;
  }
  .section2-bottom-icon img {
    height: 5rem;
  }
  .section2-bottom h2 {
    font-size: 2rem;
    text-align: center;
  }
  .section2-bottom p {
    font-family: "Raleway", sans-serif;
    font-size: 1.1rem;
    text-align: center;
  }
  /* Section4 */
  .section4-container .item {
    width: 70%;
  }
  .section4-container .item:nth-child(odd) {
    background: #f9fafb !important;
  }
  .section4-container .item:nth-child(even) {
    background: none !important;
  }
  .section3-moving-car-container .moving-car {
    width: 80%;
  }
  .wheels {
    bottom: 15.8%;
    height: 3.6rem;
    width: 3.6rem;
  }
  .wheels.left-wheel {
    left: 28.8%;
  }
  .wheels.right-wheel {
    left: 66.9%;
  }

  /* footer section */
  .footer-main {
    height: auto;
  }
  .footer-item:nth-child(2) {
    width: 50%;
    flex-direction: column-reverse;
  }

  .social-icons-footer ul li {
    height: 3.3rem;
    width: 3.3rem;
  }
  .footer-item.information span {
    display: none;
  }
  .footer-item.information .info,
  .footer-item.information .customer-service {
    margin: 0.8rem auto;
  }
  .footer-item.phone-social-icon-footer {
    width: 75%;
  }
}
@media (max-width: 450px) {
  .img-container {
    height: 38vh;
  }
  /* section2 */
  .section2-img img {
    width: 110%;
  }
  /* section3 */
  .section3-moving-car-container .moving-car {
    height: 20rem;
    width: 95%;
    bottom: 1;
  }
  .wheels.right-wheel {
    left: 70%;
  }
  .wheels.left-wheel {
    left: 24.8%;
  }
  .wheels {
    bottom: 15.5%;
    height: 3.7rem;
    width: 3.7rem;
  }
  /* contact */
  .contact {
    height: 80%;
    width: 90%;
    align-items: center;
  }
  .contact-office .contact-address {
    align-items: center;
    width: 100%;
  }
  .contact-form {
    padding-left: 0;
  }
  .contact-form div p {
    font-size: 1.2rem;
    text-align: center;
  }
  .contact-form div {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
  }
  .contact-form input,
  .contact-form input:focus {
    width: 98%;
  }
  textarea {
    width: 98%;
  }

  /* about us */
  .overview-sub .overview-img img {
    width: 90%;
  }
  .overview-main h2 {
    align-self: center;
  }
  .overview-sub .overview-text {
    width: 95%;
  }

  /* footer section */
  .footer-item-container {
    width: 95%;
  }
  .footer-item.phone-social-icon-footer .contact-footer {
    width: 90%;
  }
  .social-icons-footer {
    width: 104%;
  }
  .copyright {
    margin-top: 3rem;
  }
}
@media (max-width: 400px) {
  .section3-moving-car-container .moving-car {
    height: 50%;
    width: 110%;
  }
  .wheels.right-wheel {
    left: 74%;
  }
  .wheels.left-wheel {
    left: 21%;
  }
  .wheels {
    bottom: 14.6%;
    height: 14vmin;
    width: 14vmin;
  }
  /* footer section */
  .social-icons-footer .social-icons {
    width: 95%;
  }
  .footer-item.phone-social-icon-footer {
    width: 90%;
  }
  .footer-item-container {
    width: 98%;
  }
}
@media (max-width: 370px) {
  .wheels.right-wheel {
    left: 73.2%;
  }
  .wheels.left-wheel {
    left: 20.8%;
  }
  .wheels {
    bottom: 16%;
    height: 3.6rem;
    width: 3.6rem;
  }
}
@media (max-width: 325px) {
  .wheels {
    display: none;
    animation: none;
  }
  .limiter {
    animation: none;
  }
  .section3-moving-car-container .moving-car {
    animation: none;
    height: 20rem;
    width: 115%;
    bottom: 12;
  }
  .section3-moving-car-container .road-bg {
    animation: none;
  }
  .footer-item.phone-social-icon-footer {
    width: 100%;
  }
  .social-icons-footer .social-icons {
    width: 100%;
  }
}

